<script>
export default {
    name: "PCLogin",
    props:['phAccount','phPassword'],
    computed:{},
    created:function(){
        this.$store.commit('setUser', ['', '']); // ! 
    },
    mounted() {
        this.$refs.acfield.focus();
        this.$bus.$on('on-keyup', this.onKeyup);
    },
    beforeDestroy(){
        this.$bus.$off('on-keyup', this.onKeyup);
    },
    data: function(){
        return{
            errorText: '',
            inAccount: '',
            inPassword: '',
            isGuest: true,
            isError: false
        }
    },
    methods:{
        onAccountField(value) {
            if(value == null || value == '')return;
            if(value.toLowerCase() == 'guest')
            {
                this.$store.commit('setUser', [this.inAccount, this.inPassword]);
                this.$router.push({ name: "MainMenu" });
            }
            else
            {
                this.isGuest=false;
                this.isError=false;
                this.$nextTick(function(){
                    this.$refs.pwfield.focus();
                });
            }
        },
        onChange() {},
        onKeyup(e){
            switch(e.which){
                case 38: // ! up
                    break;
                case 40: // ! down
                    break;
                case 13: // ! enter
                    if(this.isGuest==false)
                    {
                        if(this.inAccount == 'Chi' &&
                        this.inPassword == 'wakeupsoon')
                        {
                            this.$store.commit('setUser', [this.inAccount, this.inPassword]);
                            this.$router.push({ name: "MainMenu" });
                        }
                        else
                        {
                            this.isError=true;
                            this.inAccount=this.phAccount;
                            this.inPassword=this.phPassword;
                            this.isGuest=true;
                            this.$refs.acfield.focus();
                        }
                    }
                    break;
            }
        }
    }
}
</script>


